.container{
    padding-left:60px;
    padding-right: 30px;
    display: flex;
    flex-direction: row;
    margin-bottom: 50px;
    margin-top: 50px;
    
   
}

@media only screen and (max-width: 768px) {
    .container {
        padding-left: 30px;
        padding-right: 15px;
    }
}

/* Media query pour les écrans de petite taille (par exemple, smartphones) */
@media only screen and (max-width: 480px) {
    .container {
        padding-left: 15px;
        padding-right: 15px;
        flex-direction: column;
    }
}