.service-image{
    width: 850px;
    height:480px;
}

.pro-card {
    width: 100%;
    height:480px;
}
.img{
    width: 100%;
    height: 375px;
    object-fit: cover;
}

@media (max-width: 480px){
    .service-image {
        height: 200px;
        width: 100% ;
        margin-left: 0;
        
    }  

    .pro-card {
        width: 100%;
        height:200px;
    }

    .img{
        width: 200px;
        height: 200px;
        object-fit: cover;
        justify-content: center;
    }
}
@media (min-width: 768px){
    .service-image {
        height: 300px;
        width: 35% ;
        margin-left: 0;
        
    }  

    .pro-card {
        width: 100%;
        height:200px;
    }

    .img{
        width: 100%;
        height: 200px
    }
}
