
.card {
    position: relative;
    overflow: hidden;
   
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    height: 280px; 
  
   
  }
  
  
  
  .card-image {
    /* width: 100%; */
    height: 100%;
    width: 480px;
    display: block;
    filter: brightness(0.5); /* Assombrir l'image par défaut */
    transition: filter 0.7s;
    object-fit: cover;
   
  }
  
  .card:hover  .card-image{
    filter: brightness(0.9);
    transition: transform 0.7s ease-in-out;
     transform: scale(1.3)
  }
  .card-text {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background:
     rgba(0, 0, 0, 0.7);
      color: #fff;
    padding: 15px;
    text-align: center;
    transition: transform 0.3s;
  }
  
   .card-text {
    transform: scale(1.1); /* Agrandir le texte au survol */
  }
  
  .card-text h2 {
    margin: 0;
    font-size: 1.2rem;
    transition: font-size 0.7s;
  }
  
  .card:hover .card-text h2 {
    font-size: 1.5rem; /* Agrandir la taille du texte au survol */
  }
  
  .card-text p {
    margin: 0;
    font-size: 0.8rem;
  }
  
  .card:hover .card-text p {
    font-size: 1.2rem; /* Agrandir la taille du texte au survol */
  }
  