.image {
    height: 650px;
    width: 100%  ;
    object-fit: 'cover';
    position: 'relative';
}
@media (max-width: 780px){
    .image {
        height: 450px;
        width: 100%  ;
        object-fit: 'cover';
        position: 'relative';
        margin-top: 30px;
    }  
}
@media (max-width: 480px){
    .image {
        height: 250px;
        width: 100%  ;
        object-fit: 'cover';
        position: 'relative';
        margin-top: 30px;
    }  
}