.responsive-container {
    background-color: #000;
  
}

.card-container {
    display: flex;
    justify-content: center;
}

@media (max-width: 768px) {
    .responsive-container {
        padding-left: 20px;
        padding-right: 20px;
       
    }

    .card-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center
    }
}
